export default "import { isIntersection } from '@lunit/is-intersection';\n\ntype Point = [number, number];\n\n/**\n * polygonì´ êµì°¨ì ì„ ê°€ì§„ complex polygonì¸ì§€ í™•ì¸\n * @param polygon Array<[number, number]>\n */\nexport function isComplexPolygon(polygon: Point[]): boolean {\n  // ëì ìœ¼ë¡œ ìµœì´ˆì ì„ ì¶”ê°€í•´ì„œ ë‹«íŒ polygonì„ ë§Œë“¤ì–´ì¤€ë‹¤\n  const closedPolygon: Point[] = [...polygon, polygon[0]];\n  const max: number = closedPolygon.length;\n\n  // 0 ->\n  let i: number = -1;\n  while (++i < max - 2) {\n    // i + 1 ->\n    // í˜„ì¬ i ì§€ì ì˜ ì´ì „ì€ ê²€ìƒ‰í•  í•„ìš”ê°€ ì—†ê¸° ë•Œë¬¸ì— i ì´í›„ ì§€ì ë¶€í„° ê²€ìƒ‰í•œë‹¤\n    let n: number = i + 2;\n    while (++n < max - 1) {\n      // line a -> b ì™€ line c -> d ê°€ êµì°¨í•˜ëŠ”ì§€ ì²´í¬í•œë‹¤\n      // i ê°€ point a ê°€ ë˜ê³ , i + 1 ì´ point b ê°€ ëœë‹¤\n      // i + 2 ê°€ c, i + 3 ì´ dê°€ ëœë‹¤\n      // i + 1 -> i + 2 ì€ i -> i + 1 ê³¼ ë§ë‹¿ì•„ìˆì–´ êµì°¨ë  ìˆ˜ ì—†ìœ¼ë¯€ë¡œ ì œì™¸í•œë‹¤\n      if (isIntersection(closedPolygon[i], closedPolygon[i + 1], closedPolygon[n], closedPolygon[n + 1])) {\n        return true;\n      }\n    }\n  }\n\n  return false;\n}\n"